@-webkit-keyframes BeforeSquares {
  from {
    top: 10%; }
  to {
    top: 15%; } }

@keyframes BeforeSquares {
  from {
    top: 10%; }
  to {
    top: 15%; } }

@-webkit-keyframes AfterSquares {
  from {
    top: 30px; }
  to {
    top: 100px; } }

@keyframes AfterSquares {
  from {
    top: 30px; }
  to {
    top: 100px; } }
  #ayan-timer .square-block {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 15;
    top: 0; }
  #ayan-timer .square-block > .before-square {
    background: linear-gradient(to right, #efefef 0%, #ffffff 100%);
    display: block;
    position: absolute;
    width: 200px;
    height: 200px;
    right: -90px;
    top: 15%;
    -webkit-transform: rotate(25deg);
            transform: rotate(25deg);
    -webkit-animation: BeforeSquares 3s ease-in-out alternate infinite;
            animation: BeforeSquares 3s ease-in-out alternate infinite;
    z-index: 21; }
  #ayan-timer .square-block > .after-square {
    background: linear-gradient(to right, #efefef 0%, #ffffff 100%);
    display: block;
